import './styles.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import * as dat from 'dat.gui'

const gui = new dat.GUI()
gui.close()
const parameter = {
    color : 0xf22424 
    , recColor : 0xffffff
    , hemisphereColor1 : 0x6b6b6b ,
     hemisphereColor2  : 0x080820,

}

const fontLoader = new THREE.FontLoader()
fontLoader.load('fonts\\helvetiker_regular.typeface.json' ,(font) => {
    const textGeometry = new THREE.TextBufferGeometry( 'RBC\'s Team', {
		font: font,
		size: 0.5,
		height: 0.2,
		curveSegments: 20,
		bevelEnabled: true,
		bevelThickness: 0.03,
		bevelSize: 0.02,
		bevelOffset: 0,
		bevelSegments: 4
})
    textGeometry.center()
    const material = new THREE.MeshStandardMaterial({color : parameter.color})
    gui.addColor(parameter , 'color').onChange(() => {
        material.color.set(parameter.color)
    })
    const text = new THREE.Mesh(textGeometry , material)
    scene.add(text)
    
    const torusGeometry = new THREE.TorusBufferGeometry(0.3 , 0.2 , 20 ,45 )

    for (let i = 0 ; i< 2000 ; i++) {
        const donut = new THREE.Mesh(torusGeometry , material)
        donut.position.x = (Math.random() - 0.5) *100
        donut.position.y = (Math.random() - 0.5) *100
        donut.position.z = (Math.random() - 0.5) *100

        donut.rotation.x = Math.random() * Math.PI
        donut.rotation.y = Math.random() * Math.PI

        const scale = Math.random()
        donut.scale.set(scale,scale,scale)
        scene.add(donut)
    }
})


const size = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('dblclick', () => {
    const fullScreen = document.fullscreenElement || document.webkitFullscreenElement
    if (!fullScreen) {
        if (canvas.requestFullscreen) {
            canvas.requestFullscreen()
        }
        else if (canvas.webkitRequestFullscreen) {
            canvas.webkitRequestFullscreen()
        }
    }
    else {
        if (document.exitFullscreen)
        document.exitFullscreen()
        else if (document.webkitExitFullscreen)
        document.webkitExitFullscreen()
    }
})

window.addEventListener('resize', () => {
    size.width = window.innerWidth;
    size.height = window.innerHeight
    camera.aspect = size.width / size.height
    camera.updateProjectionMatrix()
    renderer.setSize(size.width, size.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

const scene = new THREE.Scene()

const ambientLight = new THREE.AmbientLight(0xffffff , 0.3)
gui.add(ambientLight, 'intensity' , 0 , 1 , 0.01).name('ambientLightIntensity');

const directionalLight = new THREE.DirectionalLight(0xffffff , 0.3)
directionalLight.position.set(2,2,-1)
directionalLight.shadow.mapSize.width = 1024
directionalLight.shadow.mapSize.height = 1024
directionalLight.castShadow = true

directionalLight.shadow.camera.near =1 
directionalLight.shadow.camera.far = 6
directionalLight.shadow.camera.right = 2
directionalLight.shadow.camera.left= -2
directionalLight.shadow.camera.top= 2
directionalLight.shadow.camera.bottom= -2

const directionalLightCameraHelper = new THREE.CameraHelper(directionalLight.shadow.camera)
directionalLightCameraHelper.visible = false
scene.add(directionalLightCameraHelper)

gui.add(directionalLight, 'intensity' , 0 , 1 , 0.01).name('directionalIntensity');
gui.add(directionalLight.position , 'x' , -5 , 5 ,0.0001).name("directional  x")
gui.add(directionalLight.position , 'y' , -5 , 5 ,0.0001).name("directional  y")
gui.add(directionalLight.position , 'z' , -5 , 5 ,0.0001).name("directional  z")
const spotLight = new THREE.SpotLight(0xffffff , 0.3 ,10 , Math.PI * 0.2)
spotLight.castShadow = true
spotLight.position.set(0 ,2 , 2)
spotLight.shadow.mapSize.width = 1024;
spotLight.shadow.mapSize.height = 1024;
spotLight.shadow.camera.fov = 30
spotLight.shadow.camera.near = 1
spotLight.shadow.camera.far = 6

gui.add(spotLight, 'intensity' , 0 , 1 , 0.01).name('spotLightIntensity');
gui.add(spotLight.position , 'x' , -5 , 5 ,0.0001).name("spotLight x")
gui.add(spotLight.position , 'y' , -5 , 5 ,0.0001).name("spotLight  y")
gui.add(spotLight.position , 'z' , -5 , 5 ,0.0001).name("spotLight  z")

const spotLightCameraHelper = new THREE.CameraHelper(spotLight.shadow.camera)
spotLightCameraHelper.visible = false
scene.add(spotLightCameraHelper)
 
const pointLight = new THREE.PointLight(0xffffff , 0.3)

pointLight.castShadow = true

pointLight.position.set(-1 , 1 , 0)
pointLight.shadow.mapSize.width = 1024
pointLight.shadow.mapSize.height = 1024
pointLight.shadow.camera.near = 0.1 
pointLight.shadow.camera.far = 5

const pointLightCameraHelper = new THREE.CameraHelper(pointLight.shadow.camera)
pointLightCameraHelper.visible = false
gui.add(pointLight, 'intensity' , 0 , 1 , 0.01).name('pointLight');
gui.add(pointLight.position , 'x' , -5 , 5 ,0.0001).name("pointLight  x")
gui.add(pointLight.position , 'y' , -5 , 5 ,0.0001).name("pointLight  y")
gui.add(pointLight .position , 'z' , -5 , 5 ,0.0001).name("pointLight  z")
scene.add(pointLightCameraHelper)

const recAreaLight = new THREE.RectAreaLight(parameter.recColor , 0.4 , 20 , 20)
recAreaLight.position.set(0,2,2)
gui.addColor(parameter,'recColor').onChange(() => {
    recAreaLight.color.set(parameter.recColor)
})
recAreaLight.lookAt(new THREE.Vector3())

const hemisphereLight = new THREE.HemisphereLight( parameter.hemisphereColor1, parameter.hemisphereColor2, 1 );
gui.addColor(parameter , 'hemisphereColor1').onChange(() => {
    hemisphereLight.color.set(parameter.hemisphereColor1)
})
gui.addColor(parameter , 'hemisphereColor2').onChange(() => {
    hemisphereLight.groundColor.set(parameter.hemisphereColor2)
})
scene.add(ambientLight , directionalLight , spotLight , spotLight.target ,pointLight ,recAreaLight ,hemisphereLight)


const camera = new THREE.PerspectiveCamera(45, size.width / size.height, 0.1, 1000)
camera.position.z = 5
scene.add(camera)

const material = new THREE.MeshStandardMaterial()
material.roughness = 0.1


const canvas = document.querySelector('.webgl')
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(size.width, size.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.shadowMap.enabled = false
renderer.shadowMap.type = THREE.PCFSoftShadowMap
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
const clock = new THREE.Clock()
const tick = () => {
    const elpasedTime = clock.getElapsedTime()

    controls.update()
    renderer.render(scene, camera)
    requestAnimationFrame(tick)
}
tick()